.custom-sidebar-off{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;

}
.custom-sidebar-on{
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

